<template>
  <div
    id="app"
    :style="[
      background_url.length
        ? { backgroundImage: `url(${background_url})` }
        : { backgroundImage: `url(${base_url}/images/2/0.jpg)` }
    ]"
  >
    <main>
      <div class="search-box">
        <input
          type="text"
          class="search-bar"
          placeholder="location..."
          v-model="query"
          @keypress="fetchWeather"
        />
      </div>

      <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
        <div class="location-box">
          <div class="location">
            {{ weather.name }},{{ weather.sys.country }}
          </div>
          <div class="date">{{ dateBuilder() }}</div>
        </div>

        <div class="weather-box">
          <div class="temp">{{ Math.round(weather.main.temp) }}°c</div>
          <div class="weather">{{ weather.weather[0].description }}</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      api_key: process.env.VUE_APP_API_KEY,
      url_base: 'https://api.openweathermap.org/data/2.5/',
      query: '',
      weather: {},
      curr_time: 0,
      background_url: '',
      default_background: '/images/2/0.jpg',
      base_url: process.env.BASE_URL
    };
  },
  methods: {
    fetchWeather(e) {
      if (e.key == 'Enter') {
        var u = `${this.url_base}weather?q=${this.query}&units=metric&appid=${this.api_key}`;
        fetch(u)
          .then((res) => {
            return res.json();
          })
          .then(this.setResults);
        //console.log(u);
      }
    },
    setResults(results) {
      this.weather = results;
      this.setTime(results);
      //console.log(this.weather)
    },
    setTime(res) {
      var d = new Date();
      var localTime = d.getTime();
      var localoffset = d.getTimezoneOffset() * 60000;
      var utc = localTime + localoffset;
      var city = utc + 1000 * res.timezone;
      var currTime = new Date(city).toTimeString().split(' ')[0].split(':');
      var time = (currTime[0] + '.' + currTime[1]) * 1;
      //console.log(time);
      this.curr_time = time;
      console.log(process.env.BASE_URL);
      this.setBackground(res);
    },
    dateBuilder() {
      let d = new Date();
      let months = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December'
      ];
      let days = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
      ];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();
      return `${day} ${date} ${month} ${year}`;
    },
    setBackground(res) {
      var time = 0;
      console.log(this.curr_time);
      if (this.curr_time > 5 && this.curr_time <= 12) time = 0;
      else if (this.curr_time > 12 && this.curr_time <= 15) time = 1;
      else if (this.curr_time > 15 && this.curr_time <= 20) time = 2;
      else if (this.curr_time > 20 || this.curr_time <= 5) time = 3;
      var weather = res.weather[0].main;
      if (weather == 'Thunderstorm') weather = 3;
      else if (weather == 'Snow') weather = 2;
      else if (weather == 'Rain' || weather == 'Drizzle') weather = 1;
      else weather = 0;

      var back_url = `${this.base_url}/images/${time}/${weather}.jpg`;
      this.background_url = back_url;
      console.log(back_url);
    }
  }
};
</script>

